<template>
  <div class="swiper">
     <swiper :options="swiperOption" ref="BannerSwiper">
         <swiper-slide v-for="(item,index) in images" :key="index">
          <!-- <swiper-slide> -->
           <a href="" v-if="item.playArea == 0 || item.playArea == location">
             <div class="item_content">
               <img lazyload="" :src="item">
               <!-- <img lazyload="" src="../../assets/images/banner-0.jpeg"> -->
                <div class="img_type_tag">
                  <i class="icon">
                    <svg class="svg_icon"><use xlink:href="#svg_label_icon"></use></svg>
                  </i>
                  <span class="text">预告片</span>
                </div>
                <div class="item_count">
                     2019-08-18
                </div>
             </div>
           </a>
         </swiper-slide>
     </swiper>
      <div class="swiper-pagination swiper-page"></div><!--分页器。如果放置在swiper-container外面，需要自定义样式。-->
  </div>
</template>
<script>

export default {
  name: 'AppSwiper',
  props: ['imageData'],
  data () {
    return {
      images:[],
      swiperOption: {
        freeMode: false,
        autoplay: true,
        loop: true, // 循环模式选项
        slidesPerView: 'auto',
        freeModeSticky: true,
        // 分页器
        pagination: {
          el: '.swiper-pagination',
          bulletActiveClass: 'page-bullet-active',
          bulletClass: 'page-bullet', // 设置分页器内元素类名
          renderBullet: function (index, className) {
            return '<span class="' + className + '"></span>'
          }
        }
      },
      location: ''
    }
  },
  computed: {
    swiper () {
      return this.$refs.BannerSwiper.swiper
    }
  },
  created() {
    // console.log(localStorage.getItem('city'))
    if (localStorage.getItem('city') == 1 || localStorage.getItem('city') == 4) {
      this.location = 1
    } else if (localStorage.getItem('city') == null) {
      this.location = -1
    } else {
      this.location = 2
    }
  },
  mounted () {
    // current swiper instance
    // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
    console.log(this.imageData);
    this.images = this.imageData.map((arr,index)=>arr.contentImage)
    console.log('this is current swiper instance object', this.swiper)

  }
}
</script>
<style>
  .item_content {
    border-radius: 15px;
    overflow: hidden;
  }
  .swiper-page {
    margin-top: 5px;
  }
.swiper-page .page-bullet{
    width: 10px;
    height: 10px;
    display: inline-block;
    border-radius: 100%;
    border:1px solid rgba(180,153,66,1);
    opacity: 1;
    /* background:rgba(243,241,237,1); */
    margin-right: 8px;
}
.page-bullet-active{
  width:60px !important;
  height:10px;
  border-radius: 5px !important;
  background:rgba(180,153,66,1);
  opacity:1;
}
.swiper-page{
  width: 100%;
  left: 0px;
}
</style>



