<template>
  <div class="feeds_block">
    <div class="title">{{horizontalData.title}}</div>
    <swiper :options="swiperOption" class="scroll-horizontal" ref="actorSwiper">
      <!-- slides -->
      <swiper-slide v-for="(item,index) in horizontalData.elements" :key="index">
        <div class="screenLabel" @click="onClick(index, $event)" v-if="item.playArea == 0 || item.playArea == location">
          <a class="img_box">
            <img :src="item.contentImage" />
            <i class="line"></i>
          </a>
          <span>{{item.title}}</span>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
  export default {
    name: 'HorizontalRecommend',
    props: ['horizontalData'],
    data() {
      return {
        swiperOption: {
          freeMode: true,
          slidesPerView: 'auto',
          freeModeMomentumRatio: 0.5,
          freeModeMomentum: false,
          spaceBetwwen:2,
        },
        location: ''
      }
    },
    computed: {
      swiper() {
        return this.$refs.actorSwiper.swiper
      }
    },
    created() {
      // console.log(localStorage.getItem('city'))
      if (localStorage.getItem('city') == 1 || localStorage.getItem('city') == 4) {
        this.location = 1
      } else if (localStorage.getItem('city') == null) {
        this.location = -1
      } else {
        this.location = 2
      }
    },
    mounted() {
      // current swiper instance
      // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
      // console.log('this is current swiper instance object', this.swiper)
    },
    methods:{
      onClick(index,event) {
        let detailUrl= this.horizontalData.elements[index].jsonUrl
        this.$router.push({name:'detail',params:{jsonUrl:detailUrl}})
      }
    }
  }
</script>
<style scoped>
  .feeds_block {
    width: 7.5rem;
  }

  .block_title {
    /* display: flex; */
    width: 90%;
    /* margin: 0 auto; */
    /* justify-content: space-between; */
    /* align-items: flex-end; */
    margin-bottom: .12rem;
    color: #636055;
    font-size: 16px !important;
    font-weight: 0;
  }
  .title {
    display: flex;
    width: 90%;
    margin: 0 auto;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: .12rem;

    color: #636055;
    font-size: 16px;
    margin-bottom: .12rem;
  }

  .swiper-container.scroll-horizontal .swiper-slide {
    width: 2.14rem;
    margin-left: .22rem;
  }

  .screenLabel {
    display: inline-block;
    width: 100%;
    opacity: 1;
    border-radius: 100%;
     width: 2.14rem;
     height: 3.06rem;
    text-align: center;
  }

   .img_box{
    width: 2.14rem;
    height: 3.06rem;
    border-radius: .2rem;
    overflow: hidden;
  }

  .img_box img {
    width: 100%;
    height: 100%;
  }

  .screenLabel a {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 100%;
    text-align: center;
  }

  .screenLabel span {
    font-size: .24rem;
    font-family: PingFang HK;
    font-weight: 500;
    /* line-height: .26rem; */
    font-size: .26rem;
    color: #636055FF;
    opacity: 1;

    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
  }
</style>



