<template>
  <div class="container_box" v-if="recommend7Data.elements.length>0">

    <div class="recommend_7_title">
      <div class="recommend_title">{{recommend7Data.title}}</div>
      <div class="recommend_more_video" @click="toFitter" v-if="fitterUrl">更多最新视频<span class="icon"></span></div>
    </div>
    <div class="top_recommend" @click="onTopClick($event)"  v-if="topRecommendData[0].playArea == 0 || topRecommendData[0].playArea == location">
      <div class="content_img"><img :src="topRecommendData[0].contentImage" alt=""></div>
      <div class="content_title">{{topRecommendData[0].title}}</div>
      <div class="content_summary">{{topRecommendData[0].summary}}</div>
    </div>

    <div class="recommend6_box">
      <div class="content_box" v-for="(item,index) in recommend6Data" :key="index" v-if="recommend6Data.length>0" @click="onClick(index,$event)" v-show="item.playArea == 0 || item.playArea == location">
        <div class="img_box">
          <img :src="item.contentImage" alt="">
        </div>
        <div class="content_title">
          {{item.title}}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: "AppRecommend7",
    props: ['recommend7Data', 'recommend4More7'],
    data() {
      return {
        topRecommendData: '', //上部大推荐
        recommend6Data: '', //下部四小推荐
        fitterUrl: '',
        location: ''
      }
    },
    computed: {

    },
    created() {
      // console.log(localStorage.getItem('city'))
      if (localStorage.getItem('city') == 1 || localStorage.getItem('city') == 4) {
        this.location = 1
      } else if (localStorage.getItem('city') == null) {
        this.location = -1
      } else {
        this.location = 2
      }
      console.log(this.recommend5Data);
      this.fitterUrl = this.recommend4More
      this.topRecommendData = this.recommend7Data.elements.slice(0, 1);
      this.recommend6Data = this.recommend7Data.elements.slice(1);
    },
    mounted() {

    },
    methods:{
      onTopClick(event) {
        let detailUrl= this.topRecommendData[0].jsonUrl
        this.$router.push({name:'detail',params:{jsonUrl:detailUrl}})
      },
      onClick(index,event) {
        let detailUrl= this.recommend6Data[index].jsonUrl
        this.$router.push({name:'detail',params:{jsonUrl:detailUrl}})
      },
      toFitter() {
        var fitterUrl = this.fitterUrl
        this.$router.push({
          name: 'fitter',
          params: {
            jsonUrl: fitterUrl
          }
        })
      }
    }
  }

</script>
<style scoped>
.recommend_7 {
margin-bottom: .3rem
}
.icon {
display: inline-block;
background-image: url("../../assets/images/more_img.png");
width: .44rem;
height: .44rem;
background-size: cover;
background-position: .1rem;
background-position: 0 .05rem;
}
.recommend_7_title{
display: flex;
width: 90%;
margin: 0 auto;
justify-content:space-between;
align-items:flex-end;
margin-bottom: .12rem;
}
.recommend_title {
color: #636055;
font-size: 16px;
}
.recommend_more_video {
color:#B49942;
font-size: 12px;
 display: flex;
 align-items: flex-end;
}

  .container_box {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #636055;
    width: 100%;
    font-style: PingFang HK;
    margin-bottom: .2rem
  }

  .top_recommend {
    width: 6.9rem;
    margin-bottom: .2rem;
  }

  .top_recommend .content_title {
    font-size: .32rem;
    line-height: .64rem;
  }

  .summary,
  .content_title,
  .content_summary {
    font-size: .26rem;
    overflow: hidden;
    text-overflow: ellipsis;
    -ms-text-overflow: ellipsis;
    white-space: nowrap;
  }

  .content_img {
    width: 6.9rem;
    height: 3.94rem;
    border-radius: .3rem;
    overflow: hidden;
    margin-top: .2rem;
    margin-bottom: .1rem;
  }

  .content_img img {
    width: 100%;
    height: 100%;
  }



  .container_box {
    display: flex;
    color: #636055;
    width: 100%;
    font-style: PingFang HK;
  }

  .recommend6_box {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .recommend6_box .content_box {
    width: 2.14rem;
    height: 3.8rem
  }

  .content_box .img_box {
    width: 2.14rem;
    height: 3.06rem;
    border-radius: .2rem;
    overflow: hidden;
  }

  .img_box img {
    width: 100%;
    height: 100%;
  }

  .recommend6_box .content_box .content_title {
    text-align: center;
    font-size: 12px;
    line-height: .52rem;
  }

</style>



