import Vue from 'vue'
import Router from 'vue-router'
import Vuex from 'vuex'
import VantHomePage from '../components/index/VantHomePage.vue'
import Detail from '../components/detail/Detail.vue'
import Activity from '../components/activity/Activity.vue'
import Search from "../components/search/Search.vue"
import Fitter from "../components/fitter/Fitter.vue"
import UTV from "../components/UTV/UTV.vue"

Vue.use(Router)
Vue.use(Vuex)

export default new Router({
  base: '/freeStyleTourism/',
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/index/recommend'
    },
    {
      path: '/index/:id',
      component: VantHomePage, //VantHomePage
      // 首页
      props: true,
      meta: {
        keepAlive: true
      }
    },
    {
      path: '/search',
      name: 'search',
      component: Search,
      props: true,
      meta: {
        keepAlive: false
      }
    },
    {
      path: '/fitter',
      name: 'fitter',
      component: Fitter,
      props: true,
      meta: {
        keepAlive: false
      }
    },
    {
      path: '/detail',
      name: 'detail',
      component: Detail,
      props: true,
      meta: {
        keepAlive: false
      }
    },
    {
      path: '/activity',
      name: 'activity',
      component: Activity,
      props: true,
      meta: {
        keepAlive: false
      }
    },
    {
      path: '/UTV',
      name: 'UTV',
      component: UTV,
      props: true,
      meta: {
        keepAlive: false
      }
    }
  ]
})



