<template>
  <div class="container">
    <!-- <img class="video-back" @click="back" src="../../assets/images/back_w.png"> -->
    <VideoLabel ref="VideoLabel" :videoLabelData='videoLabelData' v-if="flag"></VideoLabel>
    <VideoName :videoNameData='videoNameData' v-if="flag"></VideoName>
    <ActorList :actorListData='actorListData' v-if="haveActorListData"></ActorList>
    <ItemList :itemListData='itemListData' :videoItemData='videoItemData' :videoNameData='videoNameData' v-if="flag"></ItemList>
    <MoreRecommend :moreRecommendData='moreRecommendData' v-if="flag"></MoreRecommend>
    <img class="activity" src="../../assets/images/activity.png" @click="toActivity" alt="">
  </div>
</template>

<script>
import VideoLabel from './DetailPageType/VideoLabel'
import VideoName from './DetailPageType/VideoName'
import ItemList from './DetailPageType/ItemList'
import MoreRecommend from './DetailPageType/MoreRecommend'
import ActorList from './DetailPageType/ActorList'
export default {
  components: {VideoLabel, VideoName, ItemList, ActorList, MoreRecommend},
  data() {
    return {
      dataUrl: '',
      videoLabelData: '',
      videoItemData: 0, // 第几集
      videoNameData: '',
      itemListData: '',
      haveActorListData: '',
      actorListData: '',
      moreRecommendData: '',
      flag: false,
      fromPage: ''
    }
  },
  props: ['jsonUrl'],
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.from(from.path)
    })
  },
  beforeRouteLeave (to, from, next) {
    this.$refs.VideoLabel.timeEnd()
    next()
  },
  created() {
    var _hmt = _hmt || [];
    (function() {
      var hm = document.createElement("script");
      hm.src = "https://hm.baidu.com/hm.js?4926cf3d2b853ff341b5b083520ef6a1";
      var s = document.getElementsByTagName("script")[0];
      s.parentNode.insertBefore(hm, s);
    })();
  },
  mounted() {
    if (this.$route.params.jsonUrl) {
      this.dataUrl = this.$route.params.jsonUrl
      localStorage.setItem('dataUrl', this.$route.params.jsonUrl)
      // let url = "/api/?" + this.dataUrl.split("?")[1]
      let url = this.dataUrl
      this.init(url)
    } else {
      this.dataUrl = localStorage.getItem('dataUrl')
      let url = this.dataUrl
      this.init(url)
    }
  },
  methods: {
    init(url) {
      this.dataUrl = url
      url += '&l=zh_CN'
      this.axios.get(url).then((res) => {
        console.log(res)
        this.videoLabelData = res.data.data.item.list[this.videoItemData].jsonUrl
        this.videoNameData = res.data.data.detail
        this.itemListData = res.data.data.item.list
        this.haveActorListData = res.data.data.detail.actor
        // this.actorListData = res.data.data.actors.jsonUrl
        this.actorListData = res.data.data.detail.actor
        this.moreRecommendData = url
        this.flag = true
      }).catch((error) => {
        console.log(error)
      })
    },
    back() {
      if (this.fromPage == '/activity') {
        this.$router.push({
          path: '/index/recommend'
        })
      } else {
        this.$router.push({
          path: this.fromPage
        })
      }
    },
    toActivity() {
      let detailUrl= this.$route.params.jsonUrl
      this.$router.push({
        name: 'activity',
        params: {
          jsonUrl: detailUrl
        }
      })
    },
    from(fromPage) {
      this.fromPage = fromPage
    }
  }
}
</script>

<style scoped>
.container {
  margin: 0px;
  padding: 0px;
}
.activity {
  position: fixed;
  width: 1.4rem;
  top: 7.6rem;
  left: 0;
  z-index: 400;
}
.video-back {
  width: .48rem;
  height: .48rem;
  position: absolute;
  top: .3rem;
  left: .3rem;
  z-index: 1;
}
</style>



