<template>
  <div class="container">
    <div class="actor_title">演员:</div>
    <swiper :options="swiperOption" class="scroll-horizontal" ref="listSwiper">
      <!-- slides -->
      <!-- <swiper-slide>
        <div class="list">
          <div class="list_img" :style="'backgroundImage: url(' + item.contentImage + ')'"></div>
          <div class="list_img"></div>
          <div class="list_name">周星驰</div>
        </div>
      </swiper-slide>
      <swiper-slide>
        <div class="list">
          <div class="list_img" :style="'backgroundImage: url(' + item.contentImage + ')'"></div>
          <div class="list_img"></div>
          <div class="list_name">周星驰</div>
        </div>
      </swiper-slide> -->

      <!-- <swiper-slide v-for="(item, index) in actorData" :key="index" ref="swiper">
        <div class="list_img" :style="'backgroundImage: url(' + item.profileImage + ')'"></div>
        <div class="list_name">{{ item.name }}</div>
      </swiper-slide> -->
    </swiper>
    <div class="actor_name">{{ actorData }}</div>
  </div>
</template>

<script>
export default {
  name: 'ActorList',
  props: ['actorListData'],
  data() {
    return {
      swiperOption: {
        freeMode: true,
        slidesPerView: 'auto',
        freeModeMomentumRatio: 0.5,
        initialSlide: ''
      },
      actorData: ''
    }
  },
  computed: {
    swiper () {
      return this.$refs.listSwiper.swiper
    }
  },
  created() {
    // let url = this.actorListData
    // this.axios.get(url).then((res) => {
    //   console.log(res)
    //   this.actorData = res.data.data.actors
    // }).catch((error) => {
    //   console.log(error)
    // })
    this.actorData = this.actorListData
  },
  mounted () {
    // current swiper instance
    // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
    console.log('this is current swiper instance object', this.swiper)
    this.init()
  },
  methods: {
    init() {
      // console.log(this.itemListData)
      // console.log(this.videoItemData)
      // this.listSwiperData = this.itemListData
      // this.videoItem = this.videoItemData
    }
  }
}
</script>

<style scoped>
.container {
  margin: 0;
  padding: 0;
}
.actor_title {
