  margin-top: .2rem;
  margin-left: .3rem;
}
.actor_name {
  color: #636055;
  font-size: .28rem;
  margin-top: .2rem;
  margin-left: .3rem;
  margin-right: .3rem;
}
.list {
  width: 1.2rem;
}
.swiper-container.scroll-horizontal .swiper-slide{
  width: 1.2rem;
  margin-left: .3rem;
}
.list_img {
  width: 1.2rem;
  height: 1.2rem;
  margin-top: .1rem;
  background-size: cover;
  background-position: center;
  border-radius: 50%;
  background-image: url('../../../assets/images/zxc.png');
}
.list_name {
  color: #636055;
  font-size: .26rem;
  font-weight: 600;
  text-align: center;
}
</style>



