<template>
  <div class="history_box">
    <div v-if=haveSearchData>
      <div class="top_box">
        <span class="word">搜索历史</span><span class="icon" @click="deleteHistory"></span>
      </div>
      <div class="search_list">
        <div class="content_box" v-for="(item, index) in historyData" :key="index" v-if="index < 10" @click="onClick(item)">
          {{item}}
        </div>
      </div>
      <div class="bottom_box">
        <!-- ————————最多保存最新的10个搜索记录———————— -->
      </div>
    </div>


    <div class="search_alert" v-show=!haveSearchData>
      <p>未搜索到相关视频</p>
      <p>请换个词再试试吧</p>
    </div>
  </div>


</template>

<script>
  export default {
    name: "SearchHistory",
    props: ['haveSearchData'],
    data() {
      return {
        // haveSearchData:true,
        historyData: []
      }
    },
    created() {

    },
    mounted() {
      if(sessionStorage.getItem("historyData")){
        this.historyData = JSON.parse(sessionStorage.getItem("historyData")).reverse();
      } else {

      }
    },
    methods: {
      onClick(value) {
        this.$parent.historySearch(value)
      },
      deleteHistory() {
        sessionStorage.clear()
        this.historyData = []
      }
    }
  }
</script>

<style scoped>
  .history_box {
    margin: .5rem 0.4rem 0 0.4rem;
    font-style: PingFang HK;
  }
  .search_alert {
    margin-top: 2.4rem;
    margin-bottom: 1.4rem;
    text-align: center;
    font-size: 18px;
    line-height: 30px;
    font-weight: 600;
    opacity: 0.7;
  }
  .top_box {
    margin-bottom: .4rem;
  }
  .top_box .icon {
     float: right;
     background-image: url("../../assets/images/delete_img.png");
     width: .32rem;
     height: .32rem;
     vertical-align:top;
     margin-top: .08rem;
     background-repeat: no-repeat;
  }
  .top_box .word {
    font-size: 18px;
    font-weight: 600;
    color: #636055;
  }
  .search_list {
    color: #636055;
    font-size: .28rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    /* margin-bottom: .3rem; */
  }

  .search_list .content_box {
    margin-right: .2rem;
    padding: 0 .15rem;
    line-height: 28px;
    background-color: #636055;
    color: #FFFFFF;
    border-radius: .3rem;
    margin-bottom: .2rem;

    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .bottom_box {
    width: 6.7rem;
    text-align: center;
    font-size: .12px;
    color:rgb(206, 206, 202);
  }
</style>



