<template>
  <div class="container">
    <div class="recommend_title">热播推荐</div>
    <div class="recommend_lists">
      <div class="recommend_list" v-for="(item, index) in listDatas" :key="index" @click="onClick(index, $event)">
        <div class="list_img" :style="'backgroundImage: url(' + item.assetImg + ')'">
          <!-- <img :src="item.assetImg" alt=""> -->
        </div>
        <div class="list_text">
          <div class="list_text_title">{{ item.assetName }}</div>
          <!-- <div class="list_text_content">{{ item.description }}</div> -->
        </div>
      </div>
    </div>
    <!-- <div class="clear"></div> -->
  </div>
</template>

<script>
export default {
  name: 'MoreRecommend',
  props: ['moreRecommendData'],
  data() {
    return {
      listData: '',
      listDatas: []
    }
  },
  created() {
    this.init()
  },
  mounted() {

  },
  methods: {
    init() {
      console.log(this.moreRecommendData)
      let url = this.moreRecommendData.replace(/mAsset/, 'mAssetRecommend')
      this.axios.get(url).then((res) => {
        console.log(res)
        this.listData = res.data.data.assets
        if (localStorage.getItem('city') == 0) {
          for (let i = 0; i < this.listData.length; i++) {
            if (this.listData[i].playArea == '2') {
              this.listDatas.push(this.listData[i])
            } else if (this.listData[i].playArea == '0') {
              this.listDatas.push(this.listData[i])
            }
          }
        } else if (localStorage.getItem('city') == 1 || localStorage.getItem('city') == 4) {
          for (let i = 0; i < this.listData.length; i++) {
            if (this.listData[i].playArea == '1') {
              this.listDatas.push(this.listData[i])
            } else if (this.listData[i].playArea == '0') {
              this.listDatas.push(this.listData[i])
            }
          }
        } else if (localStorage.getItem('city') == null) {
          for (let i = 0; i < this.listData.length; i++) {
            if (this.listData[i].playArea == '1') {
              this.listDatas.push(this.listData[i])
            } else if (this.listData[i].playArea == '0') {
              this.listDatas.push(this.listData[i])
            }
          }
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    onClick(index, event) {
      let jsonUrl= this.listData[index].jsonUrl
      console.log(jsonUrl)
      this.$parent.videoItemData = 0
      this.$parent.init(jsonUrl)
      window.scrollTo(0, 0)
      setTimeout(() => {
        this.$parent.$children[2].slideLeft()
      }, 100)
    }
  },
  watch:{
    moreRecommendData: {
      immediate: true,
      handler (val) {
        this.init()
      }
    }
  }
}
</script>

<style scoped>
.recommend_title {
  font-size: .32rem;
  font-weight: 600;
  margin-left: .52rem;
  color: #636055;
  margin-bottom: .2rem;
}
.recommend_lists {
  margin-left: .34rem;
}
/* .recommend_list {
  width: 7.5rem;
  height: 2.1rem;
  margin-top: .18rem;
}
.list_img {
  width: 3.72rem;
  height: 2.1rem;
  float: left;
  margin-left: .3rem;
}
.list_img img {
  width: 3.72rem;
  height: 2.1rem;
  border-radius: .3rem;
} */
.recommend_list {
  width: 2.14rem;
  /* margin-top: .2rem; */
  margin-right: .2rem;
  /* float: left; */
  display: inline-block;
}
.list_img {
  width: 2.14rem;
  height: 3.06rem;
  border-radius: .2rem;
  background-size: cover;
}
.list_img img {
  width: 2.14rem;
  height: 3.06rem;
  border-radius: .2rem;
}

/* .list_text {
  width: 3.2rem;
  float: right;
}
.list_text_title {
  font-size: .26rem;
  margin-top: .16rem;
}
.list_text_content {
  margin-top: .12rem;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
} */
.list_text {
  width: 2.14rem;
  height: .72rem;
  display: table-cell;
}
.list_text_title {
  width: 2.14rem;
  font-size: .24rem;
  /* height: 1.05rem; */
  /* height: .8rem; */
  height: .72rem;
  margin-top: .06rem;
  vertical-align: middle;
  display: table-cell;
  text-align: center;


  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
}
.clear {
  clear: both;
  margin-bottom: .5rem;
}
</style>



