<template>
  <div class="container_box">
    <div class="recommend_4_title">
      <div class="recommend_title">{{recommend4Data.title}}</div>
      <div class="recommend_more_video" @click="toFitter" v-if="fitterUrl">更多最新视频<span class="icon"></span></div>
    </div>
    <div class="recommend4_box">
      <div class="content_box" v-for="(item,index) in recommend4Data.elements" :key="index" @click="onClick(index,$event)"  v-show="item.playArea == 0 || item.playArea == location">
        <div class="img_box">
          <img :src="item.contentImage" alt="">
        </div>
        <div class="content_title">
          {{item.title}}
        </div>
        <div class="content_summary">
          {{item.summary}}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: "AppRecommend4",
    props: ['recommend4Data'],
    data() {
      return {
        fitterUrl: '',
        location: ''
      }
    },
    computed: {

    },
    created() {
      // console.log(localStorage.getItem('city'))
      if (localStorage.getItem('city') == 1 || localStorage.getItem('city') == 4) {
        this.location = 1
      } else if (localStorage.getItem('city') == null) {
        this.location = -1
      } else {
        this.location = 2
      }
      console.log('4个块的', this.recommend4Data)
      console.log('4个块的查看更多', this.recommend4More)

      // this.images = this.imageData.map((arr, index) => arr.contentImage)

      this.recommend4DataList = this.recommend4Data.elements
      var index4, _index4
      for (var i = 0; i < this.recommend4DataList.length; i++) {
        if (this.recommend4DataList[i].linkType == 6) {
          index4 = i
        } else if (this.recommend4DataList[i].linkType == 8) {
          _index4 = i
        }
      }
      if (index4) {
        this.recommend4More = this.recommend4DataList[index4].jsonUrl
        console.log(this.recommend4More)
        if (index4 > -1) {
          this.recommend4DataList.splice(index4, 1)
        }
      } else if (_index4) {
        // this.recommend4More = this.recommend4DataList[_index4].jsonUrl
        this.recommend4More = 'http://223.121.14.235:81/?s=99&p=mCategorys&k=1&v=1&c=1&a=852&i=2&categoryId=' + this.recommend4DataList[_index4].linkObjId
        if (_index4 > -1) {
          this.recommend4DataList.splice(_index4, 1)
        }
      } else {
        this.recommend4More = ''
      }


      this.fitterUrl = this.recommend4More
    },
    methods: {
      onClick(index, event) {
        let detailUrl = this.recommend4Data.elements[index].jsonUrl
        this.$router.push({
          name: 'detail',
          params: {
            jsonUrl: detailUrl
          }
        })
      },
      toFitter() {
        var fitterUrl = this.fitterUrl
        if (this.$parent.isTJ) {
          this.$parent.jdgj(fitterUrl)
        } else {
          this.$router.push({
          name: 'fitter',
            params: {
              jsonUrl: fitterUrl
            }
          })
        }
      }
    }
  }

</script>
<style scoped>
  .recommend_4 {
    margin-bottom: .3rem
  }

  .icon {
    display: inline-block;
    background-image: url("../../assets/images/more_img.png");
    width: .44rem;
    height: .44rem;
    background-size: cover;
    background-position: 0 .05rem;
  }

  .recommend_4_title {
    display: flex;
    width: 90%;
    margin: 0 auto;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: .12rem;
  }

  .recommend_title {
    color: #636055;
    font-size: 16px;
  }

  .recommend_more_video {
    color: #B49942;
    font-size: 12px;
    display: flex;
    align-items: flex-end;
  }

  .container_box {
    color: #636055;
    width: 100%;
    font-style: PingFang HK;
    margin-bottom: .2rem
  }

  .recommend4_box {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .recommend4_box .content_box {
    width: 3.38rem;
    height: 2.8rem;
  }

  .content_box .img_box {
    width: 3.38rem;
    height: 1.8rem;
    border-radius: .3rem;
    overflow: hidden;
  }

  .img_box img {
    width: 100%;
    height: 100%;
  }

  .content_box .content_title {
    font-size: 13px;
    line-height: 26px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .content_box .content_summary {
    font-size: 11px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

</style>



