<template>
  <div class="feeds_video">
    <!-- 播放器 -->
    <div class="video-wrapper" :class="{'hide': isPlay == 0}">
      <div class='video-play'>
        <video
          :id="'vod-player' + randomId"
          x5-video-player-type="h5-page"
          x-webkit-airplay="allow"
          preload="auto"
          playsinline="true"
          webkit-playsinline="true"
          x5-video-player-fullscreen="false"
          x5-video-orientation="portraint"
          style="object-fit:fill"
          ref="video"
        src="http://1.203.114.102:9010/live/c8f8df0d02cf494fb355caa17c19445e/eccfedf04ba54c1581ae955b8341ebdc.m3u8"></video>
      </div>
    </div>
    <div class="item_detail">
      <div class="item_detail_left">
        <div class="TV_station">{{ live.title }}</div>
        <div class="program">{{ program }}</div>
      </div>
      <div class="item_detail_right">
        <img class="live" src="../../assets/images/live.png" alt="">
        <div class="living">正在直播</div>
      </div>
    </div>

    <div class="clear"></div>
  </div>
</template>

<script>
export default {
  name: 'app_video_label',
  props: ['liveData'],
  data() {
    return {
      player: '',
      live: '',
      program: '',
      video: '',
      playUrl: '',
      isPoster: 1,
      isPosterPlay: 1,
      isRecommend: 0,
      isPlay: 0,
      num: ''
    }
  },
  created() {
    this.init()
  },
  mounted() {
    this.video = this.$refs.video
    var width = Math.min(document.body.clientWidth, 640)
    var player = larkplayer('vod-player' + this.num)
    this.player = player
    // player.controls(false)
    // this.play()
  },
  methods: {
    init() {
      this.live = this.liveData
      let url = this.live.jsonUrl
      this.axios.get(url).then((res) => {
        console.log(res)
        this.program = res.data.data.programs[0].startTime + ' ' + res.data.data.programs[0].programName
        this.poster = res.data.data.detail.channelLogo
        this.playUrl = res.data.data.detail.livePlayurls[0].playurl
        this.player.poster(this.poster)
        this.player.src(this.playUrl)
      }).catch((error) => {
        console.log(error)
      })
    },
    play() {
      this.video.play()
      this.isPoster = 0
      this.isPlay = 1
      this.showOtherVideo()
    },
    showOtherVideo(){
      let _this = this
      // setTimeout(function(){
      //   _this.flag = _this.video.ended
      //   if (!_this.flag) {
      //     _this.showOtherVideo()
      //     console.log(_this.flag, '播放中')
      //   } else {
      //     console.log(_this.flag, '播放完毕')
      //     _this.isPoster = 1
      //     _this.isPosterPlay = 0
      //     _this.isPlay = 0
      //     _this.isRecommend = 1
      //   }
      // }, 1000)
    }
  },
  computed: {
    randomId() {
      var Num = ""
      for (var i = 0; i < 6; i++) {
        Num += Math.floor(Math.random() * 10)
      }
      this.num = Num
      return Num
    }
  }
}
</script>

<style scoped>
* {
  margin: 0px;
  padding: 0px;
}
.feeds_video {
  width: 6.9rem;
  margin: 0 auto .2rem;
  border-radius: .3rem .3rem .3rem 0;
  background: #FFFFFF;
  padding: 0;
}
video {
  position: relative;
  width: 6.9rem;
  height: 3.88rem;
  outline: none;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
.video-wrapper {
  position: relative;
  width: 6.9rem;
  height: 3.88rem;
  top: 0px;
  display: block;
  margin: 0 auto;
}
.video-pause{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-box-pack: center;
  -webkit-box-align: center;
  display: -webkit-flex;
  -webkit-align-items: center;
  -webkit-justify-content: center;
  -webkit-flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  pointer-events: none;
}
.video-play{
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.item_detail_left {
  float: left;
  position: relative;
  left: -.25rem;
}
.TV_station {
  font-size: .26rem;
  color: #636055;
  font-weight: 600;
  margin-top: .1rem;
}
.program {
  font-size: .26rem;
}
.item_detail_right {
  float: right;
  position: relative;
  right: -.25rem;
}
.live {
  width: .48rem;
  margin-top: .2rem;
  margin-right: .04rem;
}
.living {
  height: .5rem;
  color: #636055;
  font-size: .26rem;
  float: right;
  margin-top: .28rem;
}

.clear {
  clear: both;
}
/* .hide {
  display: none;
} */
</style>



