<template>
  <div class="container_box" v-if="recommend6Data.elements.length>0">
    <div class="recommend_6_title">
      <div class="recommend_title">{{recommend6Data.title}}</div>
      <!-- <div class="recommend_more_video">更多视频</div> -->
    </div>
    <div class="recommend6_box">
      <div class="content_box" v-for="(item,index) in recommendData" :key="index" v-if="recommendData.length>0" @click="onClick(index,$event)" v-show="item.playArea == 0 || item.playArea == location">
        <div class="img_box">
          <img :src="item.contentImage" alt="">
        </div>
        <div class="content_title">
          {{item.title}}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: "AppRecommend6",
    props: ['recommend6Data'],
    data() {
      return {
        topRecommendData: '', //上部大推荐
        recommendData: '', //下部四小推荐
        location: ''
      }
    },
    computed: {

    },
    created() {
      // console.log(localStorage.getItem('city'))
      if (localStorage.getItem('city') == 1 || localStorage.getItem('city') == 4) {
        this.location = 1
      } else if (localStorage.getItem('city') == null) {
        this.location = -1
      } else {
        this.location = 2
      }
      console.log(this.recommend6Data);
      this.recommendData = this.recommend6Data.elements
    },
    mounted() {

    },
    methods:{
    onClick(index,event) {
    let detailUrl= this.recommend6Data.elements[index].jsonUrl
    this.$router.push({name:'detail',params:{jsonUrl:detailUrl}})
    }
    }
  }
</script>
<style scoped>
  .recommend_6 {
    margin-bottom: .3rem
  }

  .recommend_6_title {
    display: flex;
    width: 90%;
    margin: 0 auto;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: .12rem;
  }

  .recommend_title {
    color: #636055;
    font-size: 16px;
  }

  .recommend_more_video {
    color: #B49942;
    font-size: 12px;
  }

  .container_box {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #636055;
    width: 100%;
    font-style: PingFang HK;
    margin-bottom: .2rem
  }

  .top_recommend {
    width: 6.9rem;
    margin-bottom: .2rem;
  }

  .top_recommend .content_title {
    font-size: .32rem;
    line-height: .64rem;
  }

  .summary,
  .content_title,
  .content_summary {
    font-size: .26rem;
  }

  .content_img {
    width: 6.9rem;
    height: 3.94rem;
    border-radius: .3rem;
    overflow: hidden;
    margin-top: .2rem;
    margin-bottom: .1rem;
  }

  .content_img img {
    width: 100%;
    height: 100%;
  }



  .container_box {
    display: flex;
    color: #636055;
    width: 100%;
    font-style: PingFang HK;
  }

  .recommend6_box {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .recommend6_box .content_box {
    width: 2.14rem;
    height: 3.8rem
  }

  .content_box .img_box {
    width: 2.14rem;
    height: 3.06rem;
    border-radius: .2rem;
    overflow: hidden;
  }

  .img_box img {
    width: 100%;
    height: 100%;
  }

  .recommend6_box .content_box .content_title {
    text-align: center;
    font-size: 12px;
    line-height: .52rem;

    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
  }
</style>



