<template>
  <div class="swiper_box">
    <van-swipe :autoplay="5000" :loop="true" v-if="images.length" style="width:7.5rem;height:3.88rem;border-radius:.3rem">
      <van-swipe-item v-for="(image, index) in images" :key="index" style="width:7.5rem;border-radius:.3rem" @click="onclick(index, $event)">
        <img :src='image' v-if='index == index' />
        <!-- v-lazy="image" -->
      </van-swipe-item>
      <!-- <div class="custom-indicator" slot="indicator">
      </div> -->
    </van-swipe>
  </div>

</template>

<script>
  import {
    Swipe,
    SwipeItem
  } from 'vant'
  export default {
    components: {
      [Swipe.name]: Swipe,
      [SwipeItem.name]: SwipeItem,
    },
    props: ['imageData'],
    data() {
      return {
        imageDatas: [],
        images: [

        ],
        active: 0,
        location: ''
      }
    },
    created() {
      console.log(this.imageData)
      // console.log(localStorage.getItem('city'))
      if (localStorage.getItem('city') == 0) {
        for (let i = 0; i < this.imageData.length; i++) {
          if (this.imageData[i].playArea == '2') {
            this.imageDatas.push(this.imageData[i])
          } else if (this.imageData[i].playArea == '0') {
            this.imageDatas.push(this.imageData[i])
          }
        }
      } else if (localStorage.getItem('city') == 1 || localStorage.getItem('city') == 4) {
        for (let i = 0; i < this.imageData.length; i++) {
          if (this.imageData[i].playArea == '1') {
            this.imageDatas.push(this.imageData[i])
          } else if (this.imageData[i].playArea == '0') {
            this.imageDatas.push(this.imageData[i])
          }
        }
      } else if (localStorage.getItem('city') == null) {
        for (let i = 0; i < this.imageData.length; i++) {
          if (this.imageData[i].playArea == '1') {
            this.imageDatas.push(this.imageData[i])
          } else if (this.imageData[i].playArea == '0') {
            this.imageDatas.push(this.imageData[i])
          }
        }
      }
    },
    mounted() {
      // console.log(this.imageData)
      this.images = this.imageDatas.map((arr, index) => arr.contentImage)
    },
    methods: {
      onclick(index, event) {
        let detailUrl= this.imageData[index].jsonUrl
        this.$router.push({name:'detail',params:{jsonUrl:detailUrl}})
      }
    }
  }
</script>


<style scoped>
/* img {
  width: 6.9rem;
  border-radius: .3rem;
  margin-left: .3rem;
} */
</style>



