<template>
  <div class="container_box" v-if="initData">
    <div class="top_recommend" @click="onTopClick($event)">
      <div class="title">{{initData[0].title}}</div>
      <div class="summary">{{initData[0].summary}}</div>
      <div class="top_recommend">
        <div class="content_img"><img :src="contentData[0].contentImage" alt=""></div>
        <div class="content_title">{{contentData[0].title}}</div>
        <div class="content_summary">{{contentData[0].summary}}</div>
      </div>
    </div>
    <AppRecommend4 :recommend4Data="recommend4Data"></AppRecommend4>
  </div>
</template>
<script>
  import AppRecommend4 from "../../model/app_recommend_4.vue";
  export default {
    components: {
      AppRecommend4
    },
    props: ['jsonUrl'],
    data() {
      return {
        initData: "",
        contentData: "", //推荐数据
        recommend4Data: "",
        dataUrl: this.jsonUrl
      };
    },
    created() {},
    mounted() {
      let url = this.dataUrl + '&l=zh_CN'
      this.axios.get(url).then((res) => {
        console.log(res.data.data.detail.catName);
        console.log(res.data.data.comps);
        this.initData = res.data.data.comps;
        this.contentData = this.initData[0].elements;
        this.recommend4Data = this.initData[1]
        // this.recommend5Data = this.contentData.concat(recommend4Data)
      }).catch((error) => {
        console.log(error);
      })
    },
    methods: {
      onTopClick(event) {
        let detailUrl = this.contentData[0].jsonUrl;
        this.$router.push({
          name: 'detail',
          params: {
            jsonUrl: detailUrl
          }
        })
      }
    }
  }

</script>

<style scoped>
  .container_box {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #636055;
    width: 100%;
    font-style: PingFang HK;
  }

  .top_recommend {
    width: 6.9rem;
    margin-bottom: .2rem;
  }

  .top_recommend .title {
    font-size: .32rem;
    line-height: .64rem;
  }

  .summary,
  .content_title,
  .content_summary {
    font-size: .26rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .content_img {
    width: 6.9rem;
    height: 3.94rem;
    border-radius: .3rem;
    overflow: hidden;
    margin-top: .2rem;
    margin-bottom: .1rem;
  }

  .content_img img {
    width: 100%;
    height: 100%;
  }

</style>



