<template>
  <div class="container">
    <swiper :options="swiperOption" class="scroll-horizontal" ref="listSwiper">
      <!-- slides -->
      <!-- <swiper-slide>
        <div class="lists seen">
          <a href="">1</a>
        </div>
      </swiper-slide>
      <swiper-slide>
        <div class="lists looking">
          <a href="">2</a>
        </div>
      </swiper-slide>
      <swiper-slide>
        <div class="lists notSeen">
          <a href="">3</a>
        </div>
      </swiper-slide> -->
      <swiper-slide v-if="show" :class="{hide: listSwiperData.length == 1}" v-for="(item, index) in listSwiperData" :key="index" ref="swiper">
        <div ref="list_swiper" :class="{lists: true, seen: index < videoItem, looking: index == videoItem, notSeen: index > videoItem}" @click="onClick(index, $event)">
          <a>{{ index + 1 }}</a>
        </div>
      </swiper-slide>
    </swiper>

    <div class="more">
      <div class="anthology" :class="{hide: listSwiperData.length == 1}" @click="showList">
        查看选集列表<img src="../../../assets/images/more_img.png" alt="">
      </div>
      <!-- <div class="collection" @click="collection">
        <img src="../../../assets/images/sc.png" alt="">
      </div> -->
      <div class="share" @click="share_wechat">
        <img src="../../../assets/images/fx.png" alt="">
      </div>
    </div>

    <div class="list" ref="list">
      <div class="list_select">选集</div>
      <img class="list_close" @click="closeList" src="../../../assets/images/close.png" alt="">
      <ul>
        <li :class="{seen: index < videoItem, looking: index == videoItem, notSeen: index > videoItem}" @click="onClick(index, $event)" v-for="(item, index) in listSwiperData" :key="index">
          <a>{{ index + 1 }}</a>
        </li>
      </ul>
    </div>

    <div class="share_list" ref="share">
      <div class="share_list_title">即刻分享</div>
      <ul class="share_list_content">
        <li class="share_wechat" @click="share_wechat">微信</li>
        <li class="share_friends">朋友圈</li>
        <li class="share_blog">微博</li>
      </ul>
      <div class="share_list_cancel" @click="cancel">取消</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ItemList',
  props: ['itemListData', 'videoItemData', 'videoNameData'],
  data () {
    return {
      swiperOption: {
        freeMode: true,
        slidesPerView: 'auto',
        freeModeMomentumRatio: 0.5,
        initialSlide: ''
      },
      listSwiperData: '',
      videoItem: '',
      sharaUrl: '',
      show: 1
    }
  },
  computed: {
    swiper () {
      return this.$refs.listSwiper.swiper
    }
  },
  created() {

  },
  mounted () {
    // current swiper instance
    // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
    console.log('this is current swiper instance object', this.swiper)
    this.init()
  },
  methods: {
    init() {
      console.log(this.itemListData)
      console.log(this.videoItemData)
      this.listSwiperData = this.itemListData
      this.videoItem = this.videoItemData

      this.sharaUrl = this.$parent.dataUrl

      if (this.listSwiperData.length > 30) {
        this.$refs.list.style.height = (this.listSwiperData.length / 5) + 'rem'
      }

    },
    share() {
      this.$refs.share.style.display = 'block'
    },
    share_wechat() {
      var json = eval('(' + Jegotrip.getUserInfo() + ')')
      Jegotrip.share(
        JSON.stringify({
          'title': '看视频，即享无忧好礼', //this.videoNameData.assetName,
          'desc': '年度福利！1500万无忧币拿到手软，点击领取！#' + this.videoNameData.assetName + '#', //this.videoNameData.description,
          'url': 'http://ishow.jegotrip.com.cn:10086/freeStyleTourism/share/index.html?' + this.sharaUrl + '&userId=' + json.userCode,
          'pic': this.videoNameData.assetImg,
          'fn': ''
        })
      )
    },
    cancel() {
      this.$refs.share.style.display = 'none'
    },
    collection() {
      alert('收藏')
    },
    onClick(index, event) {
      this.$parent.videoItemData = index
      let jsonUrl = this.listSwiperData[index].jsonUrl.replace(/mAssetItemVods/, 'mAsset')
      this.$parent.init(jsonUrl)
      window.scrollTo(0, 0)
    },
    slideLeft() {
      this.$refs.listSwiper.swiper.slideTo(0)
    },
    showList() {
      this.$refs.list.style.display = 'block'
      this.show = 0
    },
    closeList() {
      this.$refs.list.style.display = 'none'
      this.show = 1
    }
  },
  watch:{
    itemListData: {
      immediate: true,
      handler (val) {
        this.init()
      }
    },
    videoItemData: {
      immediate: true,
      handler (val) {
        this.init()
      }
    }
  }
}
</script>
<style scoped>
.swiper-container.scroll-horizontal .swiper-slide{
  width: .9rem;
  margin-left: .16rem;
  margin-top: .16rem;
}
.lists {
  display: inline-block;
  width: 100%;
  height: .9rem;
  line-height: .9rem;
  text-align: center;
  font-size: .4rem;
  opacity: 1;
  border-radius: 30%;
}
.lists a {
  color: #000;
}
.seen {
  background: #D1CDC0;
}
.seen a {
  color: #FFF;
}
.looking {
  background: #FFD800;
}
.notSeen {
  background: #F3F1ED;
}

.more {
  width: 100%;
  height: .5rem;
  margin-top: .2rem;
  margin-bottom: .3rem;
}
.anthology {
  float: left;
  font-size: .24rem;
  line-height: .5rem;
  color: #B49942;
  margin-left: .3rem;
}
.anthology>img {
  width: .48rem;
  margin-top: .02rem;
}
.share {
  width: .6rem;
  height: .6rem;
  float: right;
  margin-right: .36rem;
}
.share img {
  width: .6rem;
  height: .6rem;
}
.collection {
  width: .6rem;
  height: .6rem;
  float: right;
  margin-right: .22rem;
}
.collection img {
  width: .6rem;
  height: .6rem;
}

.list {
  display: none;
  width: 7.5rem;
  height: 100%;
  position: absolute;
  top: 4.26rem;
  background: #FCFCFA;
  z-index: 9999;
}
.list_select {
  font-size: .3rem;
  color: #636055;
  margin-top: .2rem;
  margin-left: .3rem;
  float: left;
}
.list_close {
  width: .45rem;
  margin-top: .2rem;
  margin-right: .3rem;
  float: right;
}
.list>ul {
  margin-top: 1rem;
  margin-left: .3rem;
}
.list>ul>li {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  line-height: 1rem;
  text-align: center;
  font-size: .4rem;
  opacity: 1;
  border-radius: 30%;
  margin-right: .18rem;
  margin-bottom: .18rem;
  /* float: left; */
  display: inline-block;
}
.list>ul>li a {
  color: #000;
}

.share_list {
  display: none;
  width: 7.5rem;
  height: 5.54rem;
  position: fixed;
  bottom: 0;
  background: #FCFCFA;
  z-index: 2;
}
.share_list_title {
  font-size: .44rem;
  color: #636055;
  text-align: center;
  margin-top: .5rem;
}
.share_list_content {
  margin-top: .84rem;
  overflow: hidden;
}
.share_list_content li {
  width: .8rem;
  height: 1.2rem;
  line-height: 1.7rem;
  float: left;
  color: #636055;
  font-size: .2rem;
  text-align: center;
  background-size: .6rem .6rem;
  background-repeat: no-repeat;
  background-position: .1rem 0;
  /* padding: 0 1.1rem; */
}
.share_wechat {
  background: url(../../../assets/images/wechat.png);
  margin-left: 1.1rem;
  margin-right: 1.45rem;
}
.share_friends {
  background: url(../../../assets/images/friends.png);
  margin-right: 1.45rem;
}
.share_blog {
  background: url(../../../assets/images/blog.png);
}
.share_list_cancel {
  font-size: .32rem;
  color: #636055;
  text-align: center;
  margin-top: .9rem;
}

.hide {
  display: none;
}
</style>



