<template>
  <div class="container">
    <!-- 播放器 -->
    <div class="video-wrapper">
			<div class='video-play'>
        <video
          id="vod-player"
          x5-video-player-type="h5-page"
          x-webkit-airplay="allow"
          preload="auto"
          playsinline="true"
          webkit-playsinline="true"
          x5-video-player-fullscreen="true"
          x5-video-orientation="portraint"
          style="object-fit:fill"
          src="../../../../../test.mp4"
          ref="video"
        ></video>
        <!-- <img class="video-full" @click="requestFull" src="../../../assets/images/full.png"> -->
	  	</div>
  	</div>
  </div>
</template>

<script>

export default {
  name: 'VideoLabel',
  props: ['videoLabelData'],
  data() {
    return {
      player: '',
      video: '',
      isPoster: 1,
      isPosterPlay: 1,
      isRecommend: 0,
      isPlay: 0,
      playUrl: '',

      userInfo: '',
      userCode: '3041189', // 用户ID
      time: 0,
      timer: '',
      timeLength: 0
    }
  },
  created() {
    this.userInfo = eval('(' + Jegotrip.getUserInfo() + ')')
    this.userCode = this.userInfo.userCode
    var _this = this
    this.video = this.$refs.video // 拿到播放器
    this.init()
    this.timeStart()
  },
  mounted() {
    var player = larkplayer('vod-player')
    this.player = player
    // player.loop(true) // 循环视频播放
  },
  methods: {
    init() {
      // console.log(this.videoLabelData)
      var vod = this.videoLabelData
      this.axios.get(vod).then((res) => {
        console.log(res)
        this.playUrl = res.data.data.vodList[0].playUrl
        this.player.src(this.playUrl)
        // this.player.src('../../../../../test.mp4')
        this.player.autoplay(true) // 自动播放
        this.videoEnd()
      }).catch((error) => {
        console.log(error)
      })

      // setTimeout(() => {
        // console.warn(this.player.played().end(0))
        // console.warn(this.player.duration())
        // console.warn(this.player.paused())
        // console.warn(this.player.currentTime())
        // console.warn(this.player.remainingTime())
      // }, 5000)
    },
    sendActivity() {
      alert(this.player.played())
    },
    timing() { // 计时方法
      this.time ++
    },
    timeStart() { // 开始计时方法
      this.timer = setInterval(this.timing, 1000)
    },
    timeEnd() { // 停止计时方法
      clearInterval(this.timer)
      this.timeLength = this.time / 60
      this.timeLength = this.timeLength.toFixed(1)
      console.log(this.timeLength, '提交的观看时长')

      var data = {
        userId: this.userCode,
        userWatchTime: this.timeLength
      }
      this.axios.post('http://bms-i-test.yanhuamedia.tv/uds/c/watch/update?version=1', data).then((res) => {
        console.log('提交观看时长', res)
        this.time = 0
      }).catch((error) => {
        console.log(error)
      })
    },
    videoEnd() { // 监听播放状态
      var endTime = setInterval(() => {
        if(this.player.ended() == true) {
          clearInterval(endTime)
          this.$parent.videoItemData = 1
          let jsonUrl = this.listSwiperData[1].jsonUrl.replace(/mAssetItemVods/, 'mAsset')
          this.$parent.init(jsonUrl)
          window.scrollTo(0, 0)
          this.init()
        }
      }, 1000)
    }
  },
  watch: {
    videoLabelData: {
      immediate: true,
      handler (val) {
        this.init()
      },
    }
  }
}
</script>

<style scoped>
* {
  margin: 0px;
  padding: 0px;
}
html,body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  -webkit-user-select: none;
  user-select: none;
  overflow: hidden;
  background-color: #EDEBE5;
}

/* 向下兼容 不止vw时候 写死font-size **/
/** 7.5rem === 100vw **/
html {
  height: 100%;
  font-size: 50px;
  font-size: 13.33333333vw;
  margin: 0 auto;
}

/** pc兼容 **/
@media (min-width: 560px) {
  html,body {
    font-size: 54px;
  }
}

/*** ipad兼容：ipad最大1024px **/
@media (max-width: 1024px) {
  html,body {
    max-width: 1024px;
  }
}
video {
  position: relative;
  width: 100%;
  height: 100%;
  outline: none;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  background: #000;

  width: 100%;
  height: 4.28rem;
}
.video-play {
  background: #000;
}
.video-wrapper {
  position: relative;
  width: 100%;
  height: 56vw;
  top: 0px;
  left: 0px;
  display: block;
}
.video-pause {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-box-pack: center;
  -webkit-box-align: center;
  display: -webkit-flex;
  -webkit-align-items: center;
  -webkit-justify-content: center;
  -webkit-flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  pointer-events: none;
}
.video-play {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
.video-full {
  width: .6rem;
  height: .6rem;
  position: absolute;
  top: .3rem;
  right: .3rem;
}
.hide {
  display: none;
}


.zy_media {
  position: relative;
  width: 100%;
  height: 56vw;
  top: 0px;
  left: 0px;
  display: block;
  background: #000;
}
</style>



