<template>
  <div class="itemList_container">
    <div class="recommend_lists">
      <div class="recommend_list" v-for="(item, index) in searchDatas" :key="index"
      @click="onClick(index, $event)">
        <div class="list_img" :style="'backgroundImage: url(' + item.image + ')'">
          <!-- <img :src="item.image" alt=""> -->
        </div>
        <div class="list_text">
          <div class="list_text_title">{{ item.name }}</div>
          <div class="list_text_content"></div>
        </div>
      </div>
    </div>

    <div class="search_alert" v-show="hide == 1">
      <p>由于版权限制</p>
      <p>请换个词再试试吧</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'SearchList',
    props: ['searchData'],
    data() {
      return {
        searchDatas: [],
        hide : 0
      }
    },
    created() {
      console.log(this.searchData)
      if (localStorage.getItem('city') == 0) {
        for (let i = 0; i < this.searchData.length; i++) {
          if (this.searchData[i].playArea == '2') {
            this.searchDatas.push(this.searchData[i])
          } else if (this.searchData[i].playArea == '0') {
            this.searchDatas.push(this.searchData[i])
          }
        }
      } else if (localStorage.getItem('city') == 1 || localStorage.getItem('city') == 4) {
        for (let i = 0; i < this.searchData.length; i++) {
          if (this.searchData[i].playArea == '1') {
            this.searchDatas.push(this.searchData[i])
          } else if (this.searchData[i].playArea == '0') {
            this.searchDatas.push(this.searchData[i])
          }
        }
        this.hide = 1
      } else if (localStorage.getItem('city') == null) {
        for (let i = 0; i < this.searchData.length; i++) {
          if (this.searchData[i].playArea == '1') {
            this.searchDatas.push(this.searchData[i])
          } else if (this.searchData[i].playArea == '0') {
            this.searchDatas.push(this.searchData[i])
          }
        }
        this.hide = 1
      }
    },
    mounted() {
      // console.log(this.searchData)
    },
    methods: {
      onClick(index, event) {
        let detailUrl = this.searchData[index].jsonUrl
        this.$router.push({
          name: 'detail',
          params: {
            jsonUrl: detailUrl
          }
        })
      }
    },
  }
</script>

<style scoped>
  .itemList_container {
    margin-top: .3rem;
  }
  .recommend_list {
    /* width: 100%; */
    /* height: 2.2rem; */
    width: 2.14rem;
    /* margin-top: .2rem; */
    margin-left: 0.27rem;
    float: left;
  }

  .list_img {
    width: 2.14rem;
    height: 3.06rem;
    /* margin-left: .3rem; */
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: .2rem;
  }

  .list_img img {
    width: 3.8rem;
    height: 2.2rem;
    border-radius: .26rem;
  }

  .list_text {
    width: 2.14rem;
    text-align: center;
  }

  .list_text_title {
    height: .88rem;
    font-size: .3rem;
    margin-top: .1rem;
    text-overflow: -o-ellipsis-lastline;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  .list_text_content {
    margin-top: .16rem;
    text-overflow: -o-ellipsis-lastline;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  .search_alert {
    margin-top: 2.4rem;
    margin-bottom: 1.4rem;
    text-align: center;
    font-size: 18px;
    line-height: 30px;
    font-weight: 600;
    opacity: 0.7;
  }
</style>



