<template>
  <div class="itemList_container">
    <div class="recommend_title">{{ this.moreRecommendData.title }}</div>
    <div class="recommend_lists" v-for="(item, index) in moreRecommendData.elements" :key="index">
      <div class="recommend_list" @click="onClick(index, $event)" v-if="item.playArea == 0 || item.playArea == location">
        <div class="list_img">
          <img :src="item.contentImage" alt="">
        </div>
        <div class="list_text">
          <div class="list_text_title">{{ item.title }}</div>
          <div class="list_text_content">{{ item.summary }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'MoreRecommend',
    props: ['moreRecommendData'],
    data() {
      return {
        location: ''
      }
    },
    created() {
      console.log(this.moreRecommendData)
      // console.log(localStorage.getItem('city'))
      if (localStorage.getItem('city') == 1 || localStorage.getItem('city') == 4) {
        this.location = 1
      } else if (localStorage.getItem('city') == null) {
        this.location = -1
      } else {
        this.location = 2
      }
    },
    mounted() {

    },
    methods: {
      onClick(index, event) {
        let detailUrl = this.moreRecommendData.elements[index].jsonUrl
        this.$router.push({
        name: 'detail',
        params: {
        jsonUrl: detailUrl
        }
        })
      }
    },
  }
</script>

<style scoped>
  .recommend_title {
    display: flex;
    width: 90%;
    margin: 0 auto;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: .12rem;

    color: #636055;
    font-size: 16px;
    /* font-size: 16px;
    font-weight: 600;
    margin-left: 20px; */
  }

  .recommend_list {
    width: 100%;
    height: 2.2rem;
    margin-top: 8px;
  }

  .list_img {
    width: 3.8rem;
    height: 2.2rem;
    float: left;
    margin-left: 15px;
  }

  .list_img img {
    width: 3.8rem;
    height: 2.2rem;
    border-radius: 13px;
  }

  .list_text {
    width: 2.8rem;
    margin-left: .26rem;
    float: right;
  }

  .list_text_title {
    font-size: 15px;
    margin-top: 10px;

    text-overflow: -o-ellipsis-lastline;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  .list_text_content {
    margin-top: 8px;
    text-overflow: -o-ellipsis-lastline;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
  }
</style>



