<template>
  <div class='UTV_box' v-if="initData">
    <videoLive :liveData="item" v-for="(item, index) in liveUrl" :key="index"></videoLive>
  </div>
</template>
<script>
import videoLive from '../model/app_video_live.vue'
export default  {
  name: '',
  components: {
    videoLive
  },
  props: ['jsonUrl'],
  data() {
    return {
      initData: "",
      dataUrl: this.jsonUrl,
      liveUrl: '',
      u: 0
    }
  },
  created() {
    var _this = this
    setTimeout(() => {
      var u = 0//标记上一个视频
      var video = document.querySelectorAll('video')//获取页面中所有video
      console.warn(video)

      for(let v = 0; v < video.length; v++){
        video[v].onclick = function() {
          video[u].pause()//让上一个video暂停
          video[v].play()//让当前点击的video播放
          u = v //让当前数赋值给u
          _this.u = u
        }
      }
    }, 1000)
  },
  beforeRouteLeave (to, from, next) {
    var video = document.querySelectorAll('video')//获取页面中所有video
    video[this.u].pause()
    setTimeout(() => {
      next()
    }, 200)
  },
  mounted() {
    let url = this.dataUrl
    console.log(this)
    this.axios.get(url).then((res) => {
      console.log(res)
      this.initData = res.data.data.comps
      this.liveUrl  = res.data.data.comps[0].elements
    }).catch((error) => {
      console.log(error)
    })
  }
}
</script>

<style scoped>
.UTV_box {
  padding-top: .3rem;
}
</style>



