<template>
  <div class="container_box" v-if="initData">
    <!-- <VantSwipe :imageData="imageData"></VantSwipe>
    <MoreRecommend :moreRecommendData='moreRecommendData'></MoreRecommend> -->
    <div v-for="(item, index) in contentData" :key="index">
      <VantSwipe :imageData="item.elements" v-if="item.layout == 'app_swiper_img'"></VantSwipe>
      <ScreeningLabel :screeningData="item" v-if="item.layout == 'app_screening_lable'"  ></ScreeningLabel>
      <AppRecommend4 :recommend4Data="item" v-if="item.layout == 'app_recommend_4'"></AppRecommend4>
      <AppRecommend5 :recommend5Data="item" v-if="item.layout == 'app_recommend_5'"></AppRecommend5>
      <AppRecommend6 :recommend6Data="item" v-if="item.layout == 'app_recommend_6'"></AppRecommend6>
      <AppRecommend7 :recommend7Data="item" v-if="item.layout == 'app_recommend_7'"></AppRecommend7>
      <MoreRecommend :moreRecommendData="item" v-if="item.layout == 'more_recommend'"></MoreRecommend>
      <HorizontalRecommend :horizontalData="item" v-if="item.layout == 'app_horizontal_recommend'"></HorizontalRecommend>
    </div>
  </div>
</template>
<script>
  import VantSwipe from '../../model/vant_swipe.vue'
  import ScreeningLabel from '../../model/app_screening_label'
  import AppRecommend4 from "../../model/app_recommend_4.vue"
  import AppRecommend5 from "../../model/app_recommend_5.vue"
  import AppRecommend6 from "../../model/app_recommend_6.vue"
  import AppRecommend7 from "../../model/app_recommend_7.vue"
  import HorizontalRecommend from "../../model/app_horizontal_recommend.vue"
  import MoreRecommend from "../../model/app_itemList_recommend"
  import actorRecommend from "../../model/app_actor_recommend.vue"
  export default {
    components: {
      VantSwipe, ScreeningLabel, AppRecommend4, AppRecommend5, AppRecommend6, AppRecommend7, HorizontalRecommend, MoreRecommend, actorRecommend
    },
    props: ['jsonUrl'],
    data() {
      return {
        initData: "",
        contentData: "", //推荐数据
        dataUrl: this.jsonUrl,

        imageData: '',
        moreRecommendData: '',
      };
    },
    created() {},
    mounted() {
      let url = this.dataUrl + '&l=zh_CN'
      this.axios.get(url).then((res) => {
        console.log(res.data.data.detail.catName, res.data.data.comps)
        this.initData = res.data.data.comps
        this.imageData = this.initData[0].elements

        this.contentData = this.initData
        console.log(this.contentData)

        // this.moreRecommendData = this.initData[1].elements;
      }).catch((error) => {
        console.log(error);
      })
    }
  }
</script>

<style scoped>
  .container_box {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #636055;
    width: 100%;
    font-style: PingFang HK;
    padding-bottom: .4rem;
  }

  .top_recommend {
    width: 6.9rem;
  }

  .top_recommend .title {
    font-size: .32rem;
    line-height: .64rem;
  }

  .summary,
  .content_title,
  .content_summary {
    font-size: .26rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .summary {
    margin-bottom: .2rem
  }
</style>



