<template>
  <div class="feeds_block screeningLabel">
    <div class="figures_scroll">
         <swiper :options="swiperOption" class="scroll-horizontal" ref="screenSwiper">
              <!-- slides -->
             <swiper-slide v-for="(item, index) in screeningList" :key="index" v-if="item.playArea == 0 || item.playArea == location">
               <div class="screenLabel" :class="('btn_bg_'+index)">
                 <a class="filtrate" @click="filter(item.linkObjId, item.title)">
                   <span>{{item.title}}</span>
                  <i class="line"></i>
                 </a>
               </div>
              </swiper-slide>
         </swiper>
    </div>
  </div>
</template>
<script>
export default {
  name: 'ScreeningLabel',
  props:['screeningData'],
  data () {
    return {
      swiperOption: {
        freeMode: true,
        slidesPerView: 'auto',
        freeModeMomentumRatio: 0.5,

        screeningList: '',
        categoryId: '',
        location: ''
      }
    }
  },
  created() {
    // console.log(localStorage.getItem('city'))
    if (localStorage.getItem('city') == 1 || localStorage.getItem('city') == 4) {
      this.location = 1
    } else if (localStorage.getItem('city') == null) {
      this.location = -1
    } else {
      this.location = 2
    }
    console.warn(this.screeningData.elements)
    this.screeningList = this.screeningData.elements.slice(0, -1)
    this.categoryId = this.screeningData.elements[this.screeningData.elements.length - 1].linkObjId
  },
  computed: {
    swiper () {
      return this.$refs.screenSwiper.swiper
    }
  },
  mounted () {
    // current swiper instance
    // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
    // console.log('this is current swiper instance object', this.swiper)
  },
  methods: {
    filter(tags, tagName) {
      var fitterUrl = 'http://223.121.14.235:81/?s=99&p=mCategorys&k=1&v=1&c=1&a=852&i=2&categoryId=' + this.categoryId
      this.$router.push({
      name: 'fitter',
        params: {
          jsonUrl: fitterUrl,
          tags: tags,
          tagName: tagName
        }
      })
    }
  }
}
</script>
<style scoped>
.swiper-container.scroll-horizontal .swiper-slide{
  width: 1.47rem;
  margin-right: 15px;
}
.screenLabel{
  display: inline-block;
  width: 100%;
  height: 0.7rem;
  opacity:1;
  border-radius:15px;
  line-height: 44px;
}
.btn_bg_0{
  background:linear-gradient(138deg,rgba(94,190,208,1) 0%,rgba(148,209,139,1) 100%);
}
.btn_bg_1{
  background:linear-gradient(138deg,rgba(92,186,132,1) 0%,rgba(196,206,79,1) 100%);
}
.btn_bg_2{
  background:linear-gradient(138deg,rgba(219,186,112,1) 0%,rgba(206,79,143,1) 100%);
}
.btn_bg_3{
  background:linear-gradient(138deg,rgba(219,112,174,1) 0%,rgba(133,79,206,1) 100%);
}
.screenLabel a{
  position: relative;
  display: inline-block;
  width: 100%;
  height: 100%;
  text-align: center;
}
.screenLabel span{
  font-size:12px;
  font-family:PingFang HK;
  font-weight:500;
  line-height:17px;
  color:rgba(255,255,255,1);
  opacity:1;
}
.filtrate span {
  font-size: .28rem;
}
</style>



