<template>
  <div class="search_container">
    <div class="serach_top">
      <!-- <span class="icon" @click="goBack"></span> -->
      <div class="search_box">
        <van-search v-model="value"  placeholder="请输入搜索关键词" show-action shape="round" @search="onSearch">
          <div slot="action" @click="onSearch">搜索</div>
        </van-search>
      </div>
    </div>

    <SearchHistory v-show="searchData.length<=0" :haveSearchData="haveSearchData"></SearchHistory>
    <HotSearch v-show="searchData.length<=0" v-on:hotWord="hotWord"></HotSearch>
    <SearchList :searchData="searchData" v-show="searchData.length>0"></SearchList>
  </div>

</template>

<script>
  import { Search } from 'vant';
  import SearchHistory from "./SearchHistory.vue";
  import HotSearch from "./HotSearch.vue";
  import SearchList from "./SearchList.vue"
  export default {
    components: {
      [Search.name]:Search,SearchHistory,HotSearch,SearchList
    },
    data() {
      return {
        // historyData: [],
        value:'',
        searchData:[],
        haveSearchData:true
      }
    },
    created() {
      var _hmt = _hmt || [];
      (function() {
        var hm = document.createElement("script");
        hm.src = "https://hm.baidu.com/hm.js?4926cf3d2b853ff341b5b083520ef6a1";
        var s = document.getElementsByTagName("script")[0];
        s.parentNode.insertBefore(hm, s);
      })();
    },
    mounted() {

    },
    methods: {
      goBack() {
        // history.go(-1)
        this.$router.push({
          path: '/index/recommend'
        })
      },
      onSearch() {
        console.log("搜索资产："+this.value);
        let searchUrl ="http://223.121.14.235:81/?s=99&p=mSearch&k=1&v=1&c=1&a=852&i=1&wd="+this.value + '&l=zh_CN'
        this.axios.get(searchUrl).then((res)=>{
          console.log("搜索结果：" + JSON.stringify(res.data.data))
          if(res.data.data.result.length!=0) {
              this.searchData=res.data.data.result[0].list
              this.haveSearchData=true;
          } else {
            this.haveSearchData=false;
            this.searchData=[]
          }
        }).catch((error)=>{
          console.log(error)
        })

        if (this.value != '') {
          this.SetHistorySearch(this.value)
        }
      },
      historySearch(value) {
        let searchUrl ="http://223.121.14.235:81/?s=99&p=mSearch&k=1&v=1&c=1&a=852&i=1&wd=" + value + '&l=zh_CN'
        this.axios.get(searchUrl).then((res)=>{
          console.log("搜索结果：" +JSON.stringify(res.data.data))
          if(res.data.data.result.length!=0) {
              this.searchData=res.data.data.result[0].list
              this.haveSearchData=true;
          } else {
            this.haveSearchData=false;
            this.searchData=[]
          }
        }).catch((error)=>{
          console.log(error)
        })
      },
      SetHistorySearch(value) {
        let word = value+"";
        let historyData=[];
        if(sessionStorage.getItem('historyData')) {
          historyData = JSON.parse(sessionStorage.getItem('historyData'))
          if(!historyData.includes(word)) {
             historyData.push(word);
             sessionStorage.setItem('historyData', JSON.stringify(historyData));
          }
        } else {
           historyData.push(word);
           sessionStorage.setItem('historyData', JSON.stringify(historyData));
        }

      },
      hotWord(hotWord) {
        this.value=hotWord
        this.onSearch()
      }
    }
  };

</script>

<style scoped>
  .serach_top  {
    height: 1rem;
  }
  .icon {
    display: inline-block;
    background-image: url("../../assets/images/back_img.png");
    width: .48rem;
    height: .48rem;
    margin-right: .08rem;
    position: absolute;
    left: 0.3rem;
    top: .45rem;
  }

  .search_box {
    width: 7.5rem;
    position: absolute;
    /* left: 1rem;
    top: .1rem; */
  }

  .van-search__action {
    color:#B49942;
    font-weight: 600;
    font-size: 15px;
  }


</style>



