<template>
  <div class="container">
    <!--  资产名称 -->
		<div class="video-name">
	  	<p id="video-title">{{ titleData }}</p>
      <div id="moreText" ref="moreText" @click="seeMore"><a>更多介绍<i class="seeMore"></i> &nbsp;&nbsp;</a></div>
      <div id="backText" ref="backText" @click="backSeeMore"><a>收起介绍<i class="backSeeMore"></i> &nbsp;&nbsp;</a></div>
		</div>
  	<!-- 资产简介 -->
		<div class="video-introduce">
	 	 	<div id="introduce" ref="introduce">{{ introduceData }}</div>

    </div>
  </div>
</template>
<script>
export default {
  name: 'VideoName',
  props: ['videoNameData'],
  data() {
    return {
      titleData: '',
      introduceData: ''
    }
  },
  created() {

  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      console.log(this.videoNameData)
      this.titleData = this.videoNameData.assetName
      this.introduceData = this.videoNameData.description
      setTimeout(() => {
        this.$refs.moreText.style.display = 'block'
        this.$refs.backText.style.display = 'none'
        this.$refs.introduce.style.height = '1rem'
      }, 100)
    },
    seeMore() {
      this.$refs.moreText.style.display = 'none'
      this.$refs.backText.style.display = 'block'
      this.$refs.introduce.style.height = '100%'
    },
    backSeeMore() {
      this.$refs.moreText.style.display = 'block'
      this.$refs.backText.style.display = 'none'
      this.$refs.introduce.style.height = '1rem'
    }
  },
  watch:{
    videoNameData: {
      immediate: true,
      handler (val) {
        this.init()
      }
    }
  }
}
</script>

<style scoped>
.container {
  margin: 0;
  padding: 0;
}
.video-name {
  position: relative;
  display: block;
  margin: .3rem .3rem 0;
  font-size: .4rem;
  line-height: .48rem;
  font-family: 'PingFang HK';
  color: #636055;
}
#video-title {
  width: 5.7rem;
  font-size: .4rem;
  font-weight: 600;
  float: left;
}
.seeMore {
  position: absolute;
  background-image: url('../../../assets/images/more.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: .36rem;
  height: .36rem;
  background-position-y: 3px;
  background-position-x: -4px;
  top: .05rem;
}
.backSeeMore {
  position: absolute;
  background-image: url('../../../assets/images/more.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: .36rem;
  height: .36rem;
  background-position-y: 3px;
  background-position-x: -4px;
  top: .05rem;
}
#moreText {
  position: relative;
  text-align: right;
  display: block;
}
#moreText a {
  font-size: .24rem;
  line-height: .48rem;
  font-family: PingFangSC-Semibold;
  color: #B49942;
}
#backText{
  position: relative;
  text-align: right;
  display: none;
}
#backText a {
  font-size: .24rem;
  line-height: .48rem;
  font-family: PingFangSC-Semibold;
  color: #B49942;
}

.video-introduce {
  position: relative;
  display:block;
  margin: .16rem .3rem 0;
  font-size: .24rem;
  line-height: .48rem;
  font-family: PingFangSC-Semibold;
  color: #636055;
  /* overflow: hidden; */
}
#introduce {
  position: relative;
  width:100%;
  height: 1rem;
  overflow: hidden;
}
.hover {
  overflow: visible !important;
}
</style>



