<template>
  <div class="feeds_block">
    <div class="block_title mid">{{actorData.title}}</div>
    <swiper :options="swiperOption" class="scroll-horizontal" ref="actorSwiper">
      <!-- slides -->
      <swiper-slide v-for="(item,index) in actorData.elements" :key="index">
        <div class="screenLabel btn_bg_1">
          <a class="filtrate">
            <div class="img" :style="'backgroundImage: url(' + item.contentImage + ')'"></div>
            <!-- <img :src="item.contentImage" /> -->
            <i class="line"></i>
          </a>
          <span>{{item.title}}</span>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
  export default {
    name: 'ActorLabel',
    props: ['actorData'],
    data() {
      return {
        swiperOption: {
          freeMode: true,
          slidesPerView: 'auto',
          freeModeMomentumRatio: 0.5,
          freeModeMomentum : false,
        }
      }
    },
    computed: {
      swiper() {
        return this.$refs.actorSwiper.swiper
      }
    },
    mounted() {
      // current swiper instance
      // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
      console.log('this is current swiper instance object', this.swiper)
    }
  }
</script>
<style scoped>
  .feeds_block {
    width: 7.5rem;
  }
  .swiper-container.scroll-horizontal .swiper-slide {
    width: 1.2rem;
    margin-left: .3rem;
  }

  .screenLabel {
    display: inline-block;
    width: 100%;
    height: 1.2rem;
    opacity: 1;
    border-radius: 100%;
    text-align: center;
  }

 .filtrate {
   width: .12rem;
   height: 1.2rem;
   border-radius: .6rem;

   overflow: hidden;
 }

  .filtrate .img {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
  }
  .filtrate img {
    width: 100%;
    height: 100%;
  }




  .btn_bg_1 {
    background: linear-gradient(138deg, rgba(94, 190, 208, 1) 0%, rgba(148, 209, 139, 1) 100%);
  }

  .btn_bg_2 {
    background: linear-gradient(138deg, rgba(92, 186, 132, 1) 0%, rgba(196, 206, 79, 1) 100%);
  }

  .btn_bg_3 {
    background: linear-gradient(138deg, rgba(219, 186, 112, 1) 0%, rgba(206, 79, 143, 1) 100%);
  }

  .btn_bg_4 {
    background: linear-gradient(138deg, rgba(219, 112, 174, 1) 0%, rgba(133, 79, 206, 1) 100%);
  }

  .screenLabel a {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 100%;
    text-align: center;
  }

  .screenLabel span {
    font-size: .24rem;
    font-family: PingFang HK;
    font-weight: 500;
    line-height: .26rem;
    font-size: .26rem;
    color: #636055FF;
    opacity: 1;
  }
</style>



