<template>
  <div class="hot_box">
    <div class="top_box">
      <span class="word">热门搜索</span>
    </div>
    <div class="hot_list">
      <div class="content_box" v-for="(item,index) in hotData" :key="index"
        @click="onClick(index)">
        {{item.name}}
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "HotSearch",
    data() {
      return {
        hotData:[]
      }
    },
    created() {

    },
    mounted() {
       this.axios.get("http://223.121.14.235:81/?s=99&p=mHotwords&k=1&v=1&c=1&a=852&i=1&l=zh_CN").then((res)=>{
       console.log("热搜词：" +res.data.data);
       let arr =res.data.data;
       this.hotData=arr.hotwords
       }).catch((error)=>{
       console.log(error);
       })
    },
    methods: {
      onClick(index) {
        this.$emit('hotWord',this.hotData[index].name)
      }
    }
  }
</script>

<style scoped>
  .hot_box {
    margin: .5rem 0.4rem 0 0.4rem;
    font-style: PingFang HK;
  }

  .top_box {
    margin-bottom: .4rem;
  }

  .top_box .icon {
    float: right;
    background-image: url("../../assets/images/delete_img.png");
    width: .32rem;
    height: .32rem;
    vertical-align: top;
    margin-top: .08rem;
  }

  .top_box .word {
    font-size: 18px;
    font-weight: 600;
    color: #636055;
  }

  .hot_list {
    color: #636055;
    font-size: .28rem;
  }

  .hot_list .content_box {
    margin-right: .2rem;
    border-bottom: 1px solid rgb(206, 206, 202);
    line-height: 40px;
  }

  </style>



