<template>
  <div>
    <div class="home_header">
      <div class="site_title" @click="onBack($event)">
        <span class="icon"></span>
        <!-- <div>无忧行看世界</div> -->
      </div>
      <a class="search_container" title="搜索" @click="onSearch($event)">
        <van-search placeholder="搜影视" v-model="value" shape="round" />
      </a>
      <div class="prize" @click="toActivity">观看有奖</div>
    </div>
    <div class="nav">
      <TabNav :initData="initData" :jsonUrl="jsonUrl"></TabNav>
    </div>
  </div>
</template>
<script>
  import { Search } from 'vant';
  import TabNav from '../nav/TabNav.vue'
  export default {
    components: {
      [Search.name]:Search,TabNav
    },
    data() {
      return {
        initData:[],
        itemNo:0,  //当前nav序号
        jsonUrl:'',
        value:''  //搜索值
      }
    },
    created() {
      var _hmt = _hmt || [];
      (function() {
        var hm = document.createElement("script");
        hm.src = "https://hm.baidu.com/hm.js?4926cf3d2b853ff341b5b083520ef6a1";
        var s = document.getElementsByTagName("script")[0];
        s.parentNode.insertBefore(hm, s);
      })();

      try {
        var location = eval('(' + Jegotrip.getLocalInfo() + ')')
        var city = decodeURI(location.city)
        var lat = location.lat
        var lon = location.lon
        console.log('IP地址', location, city)

        var data = {
          lat: lat,
          lon: lon,
          key: ''
        }
        this.axios.post('https://app.jegotrip.com.cn/api/ls/v1/location/geoLocation', data).then((res) => {
          console.log('位置', res)
          var city
          if (res.data.body.countryId == 7) {
            if (res.data.body.cityId == 279 || res.data.body.cityId == 280 || res.data.body.cityId == 3471) {
              city = 1
            } else {
              city = 0
            }
          } else {
            city = 4
          }

          localStorage.setItem('city', city)

          this.axios.get("http://223.121.14.235:81/?s=99&p=mHome&k=1&v=1&c=1&a=852&i=2&l=zh_CN").then((res) => {
            console.log("请求首页数据")
            console.log(res.data)
            this.initData = res.data.data.navbar
            this.jsonUrl = this.initData[this.itemNo].jsonUrl

            if (localStorage.getItem('city') == 1 || localStorage.getItem('city') == 4) {
              console.log('港澳台')
              this.location = 1
              if (this.initData.length > 3) {
                this.initData.splice(1, 2)
              }
            } else if (localStorage.getItem('city') == null) {
              console.log('app外')
              this.location = -1
              if (this.initData.length > 3) {
                this.initData.splice(1, 2)
              }
            } else {
              this.location = 2
              console.warn('中国')
            }
          }).catch((error) => {
            console.log(error)
          })
        }).catch((error) => {
          console.log(error)
        })
      } catch (error) {
        console.log(error)
        this.axios.get("http://223.121.14.235:81/?s=99&p=mHome&k=1&v=1&c=1&a=852&i=2&l=zh_CN").then((res) => {
          console.log("请求首页数据")
          console.log(res.data)
          this.initData = res.data.data.navbar
          this.jsonUrl = this.initData[this.itemNo].jsonUrl

          if (localStorage.getItem('city') == 1 || localStorage.getItem('city') == 4) {
            console.log('港澳台')
            this.location = 1
            if (this.initData.length > 3) {
              this.initData.splice(1, 2)
            }
          } else if (localStorage.getItem('city') == null) {
            console.log('app外')
            this.location = -1
            if (this.initData.length > 3) {
              this.initData.splice(1, 2)
            }
          } else {
            this.location = 2
            console.warn('中国')
          }
        }).catch((error) => {
          console.log(error)
        })
      }
    },
    mounted() {

    },
    methods:{
      onBack(event) {
        localStorage.clear()
        Jegotrip.goBack()
      },
      onSearch(event) {
        this.$router.push({name:'search'})
      },
      toActivity() {
        this.$router.push({name:'activity'})
      }
    }
  }
</script>

<style scoped>

.nav {
  margin-top: 1rem;
}


.icon  {
  display: inline-block;
  background-image: url("../../assets/images/back_img.png");
  width: .48rem;
  height: .48rem;
  margin-right: .08rem
}

.home  {
  overflow: hidden;
  float: left;
  width: 2.06rem;
  height: .48rem;
}

.prize {
  position: absolute;
  right: .24rem;
  top: .34rem;
  width: 1.2rem;
  height: .56rem;
  line-height: .56rem;
  border-radius: .3rem 0 .3rem .3rem;
  background-color: #B49942;
  padding-left: .1rem;
  color:white;
}

.home_header {
  position: fixed;
  top: 0;
  width: 7.5rem;
  height: 1.2rem;
  background: #ffffff;
  z-index: 999;
}
.home_header .site_title {
  position: absolute;
  left: .16rem;
  top: .34rem;
  line-height: .64rem;
  max-width: 10em;
  color: #636055;
  font-size: .32rem;
  text-align: left;
  font-weight: 700;
  display: flex;
  align-items: center;
}

.home_header .search_container {
  position: absolute;
  right: 1.44rem;
  top: .1rem;
  /* width: 3rem; */
  width: 6rem;
  height: .56rem;
  padding-top: 0;
  background-color: #f6f6f7;
  border-radius: .1rem;
  text-align: center;
}

.home_header .search_container .search_text {
  position: absolute;
  left: .2rem;
  line-height: .56rem;
  max-width: 2.2rem;
  color: #b3b3b3;
  font-size: .26rem;
  text-align: left
}

.home_header .search_container .icon_search {
  position: absolute;
  right: .2rem;
  top: .1rem;
  width: .4rem;
  height: .4rem;
  color: #a3a3a3;
}

</style>



