<template>
  <div class="container_box">
    <div class="nav_box">
        <van-tabs @click="onClick" v-model="active">
          <van-tab v-for="(item,index) in initData" :title="item.title" :key="index">
          </van-tab>
        </van-tabs>
    </div>

    <component v-bind:is="pageType" :jsonUrl="clickData||this.initData[0].jsonUrl" v-if="jsonUrl"></component>
  </div>
</template>
<script>
  import {
    Tab,
    Tabs
  } from 'vant'; //vant组件库
  import tj from '../index/HomePageType/tj.vue'
  import ysjp from '../index/HomePageType/ysjp.vue'
  import dm from '../index/HomePageType/dm.vue'
  import chwl from '../index/HomePageType/chwl.vue'
  import jdgj from '../index/HomePageType/jdgj.vue'
  import dsp from '../index/HomePageType/dsp.vue'
  import jl from '../index/HomePageType/jl.vue'
  export default {
    components: {
      [Tab.name]: Tab,
      [Tabs.name]: Tabs,
    },
    props: ['initData', 'jsonUrl'],
    data() {
      return {
        pageType: tj,
        data: [],
        active: 0,
        clickData: "",
        pageTypeList: [tj, jdgj, dm, dsp, chwl, jl, ysjp]
      };
    },
    beforeCreate() {
      var nav = localStorage.getItem('nav')
      if (nav == 0) {
        this.active = 0
        this.clickData = localStorage.getItem('navData')
        this.pageType = tj
      } else if (nav == 1) {
        this.active = 1
        this.clickData = localStorage.getItem('navData')
        this.pageType = jdgj
      } else if (nav == 2) {
        this.active = 2
        this.clickData = localStorage.getItem('navData')
        this.pageType = dm
      } else if (nav == 3) {
        this.active = 3
        this.clickData = localStorage.getItem('navData')
        this.pageType = dsp
      } else if (nav == 4) {
        this.active = 4
        this.clickData = localStorage.getItem('navData')
        this.pageType = chwl
      } else if (nav == 5) {
        this.active = 5
        this.clickData = localStorage.getItem('navData')
        this.pageType = jl
      } else if (nav == 6) {
        this.active = 6
        this.clickData = localStorage.getItem('navData')
        this.pageType = ysjp
      }
    },
    created() {
      // 动态获取栏目
      setTimeout(() => {
        for (let i = 0; i < this.initData.length; i++) {
          console.warn(this.initData[i].title)
        }
      }, 200)
      var nav = localStorage.getItem('nav')
      if (nav == 0) {
        this.active = 0
        this.clickData = localStorage.getItem('navData')
        this.pageType = tj
      } else if (nav == 1) {
        this.active = 1
        this.clickData = localStorage.getItem('navData')
        this.pageType = jdgj
      } else if (nav == 2) {
        this.active = 2
        this.clickData = localStorage.getItem('navData')
        this.pageType = dm
      } else if (nav == 3) {
        this.active = 3
        this.clickData = localStorage.getItem('navData')
        this.pageType = dsp
      } else if (nav == 4) {
        this.active = 4
        this.clickData = localStorage.getItem('navData')
        this.pageType = chwl
      } else if (nav == 5) {
        this.active = 5
        this.clickData = localStorage.getItem('navData')
        this.pageType = jl
      } else if (nav == 6) {
        this.active = 6
        this.clickData = localStorage.getItem('navData')
        this.pageType = ysjp
      }


      // var nav = localStorage.getItem('nav')
      // if (nav == 0) {
      //   this.active = 0
      //   setTimeout(() => {
      //     this.clickData = this.initData[0].jsonUrl
      //     this.pageType = this.pageTypeList[0]
      //   }, 0)
      // } else if (nav == 1) {
      //   this.active = 1
      //   setTimeout(() => {
      //     this.clickData = this.initData[1].jsonUrl
      //     this.pageType = this.pageTypeList[1]
      //   }, 0)
      // } else if (nav == 2) {
      //   this.active = 2
      //   setTimeout(() => {
      //     this.clickData = this.initData[2].jsonUrl
      //     this.pageType = this.pageTypeList[2]
      //   }, 0)
      // } else if (nav == 3) {
      //   this.active = 3
      //   setTimeout(() => {
      //     this.clickData = this.initData[3].jsonUrl
      //     this.pageType = this.pageTypeList[3]
      //   }, 0)
      // } else if (nav == 4) {
      //   this.active = 4
      //   setTimeout(() => {
      //     this.clickData = this.initData[4].jsonUrl
      //     this.pageType = this.pageTypeList[4]
      //   }, 0)
      // } else if (nav == 5) {
      //   this.active = 5
      //   setTimeout(() => {
      //     this.clickData = this.initData[5].jsonUrl
      //     this.pageType = this.pageTypeList[5]
      //   }, 0)
      // } else if (nav == 6) {
      //   this.active = 6
      //   setTimeout(() => {
      //     this.clickData = this.initData[6].jsonUrl
      //     this.pageType = this.pageTypeList[6]
      //   }, 0)
      // }
    },
    mounted() {

    },
    methods: {
      onClick(name, title) {
        console.log("切换导航---" + title);

        this.clickData = this.initData[name].jsonUrl;
        this.pageType = this.pageTypeList[name];
        // console.log(this.clickData)
        // console.log(this.pageType)

        if (name == 0) {
          localStorage.setItem('nav', 0)
          localStorage.setItem('navData', this.clickData)
          localStorage.setItem('navType', this.pageType)
        } else if (name == 1) {
          localStorage.setItem('nav', 1)
          localStorage.setItem('navData', this.clickData)
          localStorage.setItem('navType', this.pageType)
        } else if (name == 2) {
          localStorage.setItem('nav', 2)
          localStorage.setItem('navData', this.clickData)
          localStorage.setItem('navType', this.pageType)
        } else if (name == 3) {
          localStorage.setItem('nav', 3)
          localStorage.setItem('navData', this.clickData)
          localStorage.setItem('navType', this.pageType)
        } else if (name == 4) {
          localStorage.setItem('nav', 4)
          localStorage.setItem('navData', this.clickData)
          localStorage.setItem('navType', this.pageType)
        } else if (name == 5) {
          localStorage.setItem('nav', 5)
          localStorage.setItem('navData', this.clickData)
          localStorage.setItem('navType', this.pageType)
        } else if (name == 6) {
          localStorage.setItem('nav', 6)
          localStorage.setItem('navData', this.clickData)
          localStorage.setItem('navType', this.pageType)
        }
      },
      toFitter(url) {
        for (let i = 0; i < this.initData.length; i++) {
          if (url == this.initData[i].jsonUrl) {
            this.active = i
            this.clickData = this.initData[i].jsonUrl
            this.pageType = this.pageTypeList[i]
          }
        }
      }
    },
    watch: {
    }
  }

</script>

<style>
  .van-hairline--top-bottom::after {
    border-width: 0px 0;
  }

</style>

<style scoped>
  .container_box {
    width: 100%;
    margin-top: 2rem;
  }
  .nav_box {
    width: 100%;
    margin-bottom: 10px;
    position: fixed;
    top: 0rem;
    padding-top: 1rem;
    z-index: 999;
    left: 0;
    right: 0;
  }
</style>



